@media only screen and (max-width: 858px ) and (orientation: portrait ){
    body{
        height: 100vh;
        width: 100vw;
        overflow-x: hidden;
        margin: 0%;
        padding: 0%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-between; 
        align-items: center;
    }
    .create-post__container{
        top: 55px;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap:20px;
        width: 750px;
        height: 900px;
        background-color: #DDDFFD;
        border: 4px solid #B8AED5;
        box-shadow:0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 15px;
        padding: 10px;
        margin-top: 40px;
        margin-bottom: 10px;
        margin-left: 10px;
        margin-right: 10px;
    }
    .title-input{
        width: 90%;
        height: 55px;
        border-radius: 15px;
        border: none;
        background-color: #d0d3fe;
        font-size: 24px;
        padding: 10px;
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: 10px;
        margin-right: 10px;
    }

    .content-input{
        width: 90%;
        color : black;
        height: 600px;
        background-color: #d0d3fe;
        font-size: 24px;
        border-radius: 15px;
        border:none;
        padding: 10px;
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: 10px;
        margin-right: 10px;
    }
    .content-input::placeholder, .title-input::placeholder{
        color:black;
        font-size: 35px;
        font-weight:normal;
        text-align: justify;
    }
    .submit-note{
        width: 25%;
        height: 45px;
        border-radius: 15px;
        border: none;
        background-color: #715DAB;
        color: white;
        font-size: 20px;
        font-weight: bold;
        cursor: pointer;
        margin-top: 10px;
        margin-bottom: 10px;
        margin-left: 10px;
        margin-right: 10px;
        font-size: 24px;
    }
}
