@media only screen and (max-width: 500px ){
    body{
        height: 100vh;
        width: 100vw;
        overflow-x: hidden;
        margin: 0%;
        padding: 0%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
.profile-bar{
    display: flex;
    flex-wrap: wrap;
    text-overflow: ellipsis;
    width: 800px;
    height: 500px;
    font-size: 12px;
    justify-content: center;
}
}    
/* mediaqueries para celular termina  */
@media only screen and (max-width: 858px ) and (orientation: portrait ){
    body{
        height: 100vh;
        width: 100vw;
        overflow-x: hidden;
        margin: 0%;
        padding: 0%;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
    }
.profile-bar{
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-around;
    width: 95%;
    height: 100px;
    font-size: 12px;
    background-color:#DDDFFD;
    color : black;
    text-align: justify;
    font-size: 14px;
    border-radius: 35px;
    border: 4px solid #B8AED5;
    box-shadow:0px 4px 4px rgba(0, 0, 0, 0.25);
    padding: 10px;
    top:10px;
    left: 10px;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-left: 1px;
    margin-right: 1px;

}
.profile-photo{
    border-radius: 50%;
    border: 2px solid white;
    width: 98px;
}
.logout-btn{
    width: 200px;
    height: 40px;
    background-color: #715DAB;
    color: white;
    font-size: 24px;
    border-radius: 20px;
    border: none;
    cursor: pointer;
}
.add-note{
    position: fixed;
    bottom: 5%;
    right: 5%;
    width: 100px;
    height: 100px;
    border-radius: 50%;
}
.link-create{
    color :white;
    text-decoration: none;
    font-size: 50px;
}

}
/* mediaqueries para tablet vertical termina  */
@media only screen and (max-width: 1200px ) and (orientation: landscape ){
    body{
        height: 100vh;
        width: 100vw;
        overflow-x: hidden;
        margin: 0%;
        padding: 0%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

}
/* mediaqueries para tablet horizontal termina  */